.para .container h2::before {
  background-image: url("../assets/images/channels/islamophobiaHbg.webp");
}

.para {
  &.ar {
    ol {
      direction: rtl;
    }
  }

  ol {
    padding: 3rem;
    list-style: number;

    li {
      margin-bottom: 2rem;
      &::marker {
        font-size: 1.5rem;
      }

      p {
        margin-bottom: 1rem !important;

        &:first-child {
          margin-bottom: 2rem !important;
        }
      }
    }
  }
}

.contact-form .container .box {
  @media (min-width: 768px) and (max-width: 991px) {
    top: 137px !important;
  }
  .form input.specBtn {
    background-color: white !important;
  }
}
